@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap');


body {
  padding: 0;
  margin: 0;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}


.hero {
  visibility: visible;
  height: 100vh;
  display: grid;
  .container {
    width: 1440px;
    min-width: 1440px;
    margin: 0 auto;
    
    .hero-inner {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr;
      align-items: center;
      justify-content: space-between;
      margin: 0 24px;
      height: 100vh;

      .hero-content {
        width: 50%;
        padding-left: 30%;
        
        .hero-content-inner {
          width: 500px;
          color: #323232;
          margin: 0 auto;
          h1 {
            .hero-content-line {
              margin: 0px;
              height: 44px;
              overflow: hidden;
              .hero-content-line-inner {
                font-size: 32px;
                font-weight: 500;
                margin-bottom: 24px;
              }
              .hero-content-line-inner-second {
                font-size: 26px;
                font-weight: 500;
                margin: 0;
                padding: 0;
              }
            }}
          p {
            font-size: 14px;
            line-height: 24px;
            font-weight: 300;
            padding-right: 48px;
            margin-bottom: 5px;
          }
          .btn-row {
            position: relative;
            margin: 15px;
            &:before {
              position: absolute;
              width: 56px;
              height: 1px;
              background: #dfdfdf;
              top: 50%;
              left: -68px;
            }
            button {
              background: none;
              border: none;
              font-size: 12px;
              cursor: pointer;
              text-transform: uppercase;
              font-weight: 700;
              letter-spacing: 0.7px;
              display: flex;
              align-items: center;
              position: relative;
              &:focus {
                outline: none;
              }
              .arrow-icon {
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                background: #007fff;
                height: 48px;
                width: 48px;
                border-radius: 100px;
                margin-left: 16px;
                left: 64px;
                transform-origin: center center;
              }
            }
          }
        }
      }
      .hero-images {
        width: 50%;
        height: 100vh;
        position: relative;
        .hero-images-inner {
          .hero-image {
            position: absolute;
            overflow: hidden;
            &.girl {
              top: 0;
              right: 0px;
              width: 45%;
              height: 50%;
            }
            &.boy {
              bottom: 24px;
              left: 0px;
              width: 52%;
              height: 65%;
            }
            &.tim {
              bottom: 10px;
              left: 0px;
              width: 45%;
              height: 65%;
            }
            img {
              position: absolute;
              top: 0;
              left: 0;
              right: 0;
              bottom: 0;
              width: 100%;
            }
          }
        }
      }
    }
  }
}